* {
  padding:0;
  margin:0;
  box-sizing:border-box;
  border:0;
  background:none;
}

html {
  overflow:hidden;
}

ul {
  list-style:none;
}

header {
  display:flex;
  justify-content:center;
  align-items:center;
  position:fixed;
  width:100%;
  height:4.5rem;
  background-color:#4587e3;
  z-index:100;
}

main {
  display:flex;
  position:relative;
  padding-top:4.5rem;
  background-color:#f0f0f0;
  overflow:hidden;
}

.noData {
  width:100%;
  height:100vh;
  text-align:center;
  font-size:1.5rem;
  font-weight:bold;
  line-height:100vh;
  margin-top:-4.5rem;
}

.total {
  position:absolute;
  width:45%;
  height:4rem;
  padding:0 1rem;
  line-height:4rem;
}

.total p span {
  color:red;
  text-decoration:underline;
}

.title {
  font-size:1.3rem;
  font-weight:bold;
  color:#fff;
}

nav {
  position:relative;
  width:10rem;
  margin:0 0.7rem;
}

.yearName, .areaName {
  display:inline-block;
  width:100%;
  text-align:center;
  border:1px solid #000;
  background-color:#f0f0f0;
}

.yearName button, .areaName button {
  width:100%;
  font-size:1rem;
  font-weight:bold;
  padding:0.5rem;
  cursor:pointer;
  overflow:hidden;
}

.yearName button:hover, .areaName button:hover {
  background-color:#ddd;
}

.yearName button:after, .areaName button:after {
  content:'▼';
  float:right;
}

.yearName button.show:after, .areaName button.show:after {
  content:'▲';
}

.yearSelect, .areaSelect {
  position:absolute;
  width:100%;
  max-height:60vh;
  border:1px solid #000;
  border-top:0;
  background-color:#fff;
  overflow-y:scroll;
  z-index:10;
}

.yearSelect li, .areaSelect li {
  border-top:1px solid #000;
}

.yearSelect li:first-child, .areaSelect li:first-child {
  border-top:0;
}

.yearSelect li:hover, .areaSelect li:hover {
  background-color:#ddd;
}

.yearSelect li button, .areaSelect li button {
  width:100%;
  text-align:left;
  padding:0.5rem 1rem;
  cursor:pointer;
}

.hidden {
  display:none;
}

.rechart {
  width:50%;
  height:500px;
  padding:4rem 1rem;
}

.recharts-responsive-container {
  min-width:100px;
}

.rechart .recharts-surface {
  overflow:inherit;
}

#react-kakao-maps-sdk-map-container {
  width:55%;
  height:100vh;
  margin-top:-4.5rem;
}

.recharts-layer.recharts-cartesian-axis.recharts-xAxis.xAxis,
.recharts-layer.recharts-cartesian-axis-tick {
  white-space: pre-wrap;
  font-size:0.8rem
}